@import url('<link href="https://fonts.googleapis.com/css2?family=Montserrat:wght@900&display=swap" rel="stylesheet">')
@import url('<link href="https://fonts.googleapis.com/css2?family=Hind&display=swap" rel="stylesheet">')
body {
  margin: 0;
  font-family: 'Montserrat', sans-serif;
  font-family: 'Hind', sans-serif;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/**{padding: 0; margin: 0; box-sizing: border-box;}
body{height: 900px; background-color: #fffffe;}
*/
/**{box-sizing: border-box;}*/
body,html {
	background-color: #eff0f3;
	margin: 0;
    padding: 0;
    width: 100%;
    height: 100%;
}

.header {
	width:103%;
	background-color: #f3d2c1;
	color: #001858;
    margin: 0; 
    padding: 0;
    top: 0;
    right: 0;
}

.title {
	font-family: 'Montserrat', sans-serif;
	font-size: 3em;
	margin-bottom: 0px;
	margin-top:0px;
	text-decoration: underline;
}

.subtittle {
	font-family: 'Hind', sans-serif;
	font-size: 1em;
	margin-top: 0px;
}


.button {
	background-color: #ff8e3c;
	color: #0d0d0d;
	margin-left: 15px;
	margin-right: 15px;
	margin-top: 15px;
}

.inputFields {
	margin-left:5px;
	margin-right: 5px;
}

.item {
	display: grid;
	grid-template-columns: 1fr 1fr 1fr 1fr;
	grid-column-gap: 10px;
	-webkit-column-gap: 10px;
	        column-gap: 10px;
}

.formButton {
	background-color: #ff8e3c;
	color: #0d0d0d;
	margin-right: 15px;
	margin-left: 10px;
}

.down {
	min-width: 0px;
	overflow: scroll;
}

h4 {
	margin-top: 15px;
}
